.footer {
    padding: 80px 25px;
    &__link {
        grid-template-columns: 192px 192px 192px 445px;
        grid-template-rows: auto;
        gap: 0px;
        column-gap: 20px;
        &__email {
            grid-column: 4;
            p {
                max-width: 445px;
            }
            &__form {
                .btn {
                    &:hover {
                        cursor: pointer;
                    }
                }
                input {
                    width: 312px;
                    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
                    &:focus {
                        border: 1px solid $black;
                        box-shadow: 0px 0px 4px 0px rgba(0,0,0,0.75);
                    }
                }
            }
        }
    }
    &__soc {
        grid-template-columns: auto auto auto;
        grid-template-rows: auto;
        column-gap: 170px;
        row-gap: 30px;
    }
}