.card {
    padding: 0 15px;
    max-width: 1440px;
    margin: auto;
}
.card__item {
    position: relative;
    margin-bottom: 20px;
    padding-left: 20px;
    max-width: 349px;
    img {
        width: 100%;
        margin-bottom: 20px;
    }
}
.slick-dots {
    list-style: none;
    display: flex;
    justify-content: center;
    padding: 0;
    li {
        margin-left: 20px;
        &:nth-child(1){
            margin: 0;
        } 
    }
}

.slick-next {
    position: absolute;
    right: -45px;
    top: 230px;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    border: 2px solid black;
    color: transparent;
    background: url(../i/row1.png) no-repeat;
    background-size: 40%;
    background-position: 55% 50%;
    opacity: .2;
    &:hover {
        opacity: .6;
        border: transparent;
        box-shadow: 0 3px 10px 0 rgba(0,0,0,.1);
    }
}
.slick-prev {
    position: absolute;
    left: -45px;
    top: 230px;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    border: 2px solid black;
    color: transparent;
    background: url(../i/row2.png) no-repeat;
    background-size: 40%;
    background-position: 45% 50%;
    opacity: .2;
    &:hover {
        opacity: .6;
        border: transparent;
        box-shadow: 0 3px 10px 0 rgba(0,0,0,.1);
    }
}
