.sl {
    height: 760px;
    margin-bottom: 60px;
}
.slider-first {
    max-width: 100%;
    height: 760px;
    background-image: url(../i/slider_baner/bg-slider-02.jpeg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.slider-second {
    max-width: 100%;
    height: 760px;
    background-image: url(../i/slider_baner/bg-slider-01.jpeg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.slider-third {
    max-width: 100%;
    height: 760px;
    background-image: url(../i/slider_baner/bg-slider-03.jpeg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.slider-text {
    padding: 130px 15px;
    position: relative;
    bottom: 3rem;
    opacity: 0;
    animation: 1s swift-up ease-out forwards, 1s fade-in ease-out forwards;
    animation-delay: 1s;
    @keyframes fade-in {
        to {
          opacity: 1;
          top: 0rem;
        }
      }
    &__p1 {
        @include formText (600, 15px, 1.6);
        color: $black;
        margin-bottom: 24px;
        text-transform: uppercase
    }
    h1 {
        @include formText (600, 56px, 1.28);
        color: $black;
        margin-bottom: 20px;
    }
    &__p2 {
        @include formText (500, 18px, 1.66);
        color: #7e7e7e;
        margin-bottom: 40px;
    }
    &__btn {
        background-color: $black;
        @include formText (600, 16px, 1.65);
        color: white;
        padding: 12px 25px;
        text-align: center;
        text-decoration: none;
        border-radius: 5px;
        position: relative;
        bottom: -2rem;
        opacity: 0;
        animation: 1s swift-up ease-out forwards, 1s fade-in ease-out forwards;
        animation-delay: 1.8s;
        @keyframes fade-in {
            to {
            opacity: 1;
            bottom: 0rem;
            }
        }
        }
}