.footer {
    background-color: #f8f8f8;
    padding: 80px 15px;
    &__link {
        display: grid;
        grid-template-columns: minmax(290px, 516px);
        justify-content: center;
        gap: 40px;
        margin-bottom: 60px;
        h3 {
            @include formText (600, 20px, 1.3);
            margin-bottom: 16px;
        }
        &__company {
            &__text {
                @include formText (500, 14px, 1.85);
                margin-bottom: 16px;
                color: #7e7e7e;
            }
            &__tel {
                @include formText (800, 14px, 1.85);
            }
            &__email {
                @include formText (500, 14px, 1.85);
                color: #7e7e7e;
            }
        }
        &__useful {
            ul {
                margin-left: 0;
                padding-left: 0;
                li {
                    list-style-type: none;
                    margin-bottom: 10px;
                    @include formText (500, 15px, 1.75);
                    a {
                        color: #7e7e7e;
                        text-decoration: none;
                    }
                }
            }
        }
        &__info {
            ul {
                margin-left: 0;
                padding-left: 0;
                li {
                    list-style-type: none;
                    margin-bottom: 10px;
                    @include formText (500, 15px, 1.75);
                    a {
                        color: #7e7e7e;
                        text-decoration: none;
                    }
                }
            }
        }
        &__email {
            h3 {
                @include formText (600, 34px, 1.3);
                margin-bottom: 10px;
            }
            p {
                @include formText (500, 15px, 2);
                color: #7e7e7e;
                max-width: 516px;
                margin-bottom: 30px;
            }
            &__form {
                display: flex;
                input {
                    border: 1px solid #3f4a3c33;
                    border-radius: 3px;
                    @include formText (500, 15px, 1.6);
                    padding: 11px 20px;
                    width: 110px;
                }
                .btn {
                    background-color: $black;
                    @include formText (600, 16px, 1.6);
                    box-shadow: 0 4px 9px rgba(0,0,0,.05);
                    padding: 12px 25px;
                    text-align: center;
                    text-decoration: none;
                    border-radius: 3px;
                    color: $white;
                }
            }
        }
    }
    &__soc {
        padding: 15px;
        display: grid;
        grid-template-columns: minmax(290px, 516px);
        justify-content: center;
        gap: 20px;
        &__logo {
            width: 192px;
            height: 28px;
            grid-row: 1/2;
            img {
                width: 100%;
            }
        }
        &__link {
            display: flex;
            align-items: center;
            p {
                @include formText (500, 15px, 1.6);
                color: #7e7e7e;
            }
            a {
                margin-left: 15px;
                width: 18px;
                height: 18px;
                &:hover .soc {
                    fill: #4e7661;
                }
            }
            // &__tw {
            //     width: 18px;
            //     height: 18px;
            // }
            // &__fb {
            //     width: 18px;
            //     height: 18px;
            // }
            // &__inst {
            //     width: 18px;
            //     height: 18px;
            // }
            // &__yt {
            //     width: 18px;
            //     height: 18px;
            // }
        }
        &__pay {
            width: 290px;
            height: 26px;
            img {
                width: 100%;
            }
        }
    }
}