.info {
    display: grid;
    gap: 40px;
    justify-content: center;
    margin-bottom: 60px;
    padding: 0 15px;
    &__item {
        max-width: 310px;
        img {
            display: block;
            margin: auto;
            width: 70px;
            margin-bottom: 20px;
        }
        h2 {
            @include formText (600, 20px, 1.3);
            margin-bottom: 10px;
            text-align: center
        }
        p {
            @include formText (500, 18px, 1.6);
            color: #7e7e7e;
            text-align: center
        }
    }
}