.card {
    padding: 0 15px;
    min-width: 280px;
    max-width: 1329px;
    margin: auto;
}
.card__item {
    position: relative;
    margin-bottom: 20px;
    padding-left: 20px;
    max-width: 300px;
    img {
        width: 100%;
        margin-bottom: 20px;
    }
}
.slick-dots {
    list-style: none;
    display: flex;
    justify-content: center;
    padding: 0;
    li {
        margin-left: 20px;
        &:nth-child(1){
            margin: 0;
        } 
    }
}