.footer {
    padding: 80px 25px;
    &__link {
        grid-template-columns: 250px 250px 250px 570px;
        grid-template-rows: auto;
        gap: 0px;
        column-gap: 20px;
        margin-bottom: 80px;
        &__email {
            grid-column: 4;
            p {
                max-width: 570px;
            }
            &__form {
                input {
                    width: 436px;
                }
            }
        }
    }
    &__soc {
        grid-template-columns: auto auto auto;
        grid-template-rows: auto;
        column-gap: 320px;
        row-gap: 30px;
    }
}