.footer {
    padding: 80px 25px;
    &__link {
        grid-template-columns: 157px 157px 157px auto;
        grid-template-rows: auto;
        gap: 0px;
        column-gap: 20px;
        &__email {
            grid-column: 4;
            p {
                max-width: 370px;
            }
            &__form {
                input {
                    width: 237px;
                }
            }
        }
        &__usefulnpm {
            a:hover {
                color: $black;
                text-decoration: underline;
            }
        }
        &__info {
            a:hover {
                color: $black;
                text-decoration: underline;
            }
        }
    }
    &__soc {
        grid-template-columns: auto auto auto;
        grid-template-rows: auto;
        column-gap: 60px;
        row-gap: 30px;
        &__logo {
            grid-column: 2/3;
            margin: auto;
        }
        &__link {
            grid-column: 1/2;
        }
        &__pay {
            grid-column: 3/4;
        }
    }
}