.header {
    grid-template-columns: auto auto 30px 350px;
    width: auto;
    margin: auto;
    height: 104px;
    justify-items: inherit;
    &__menu {
        display: none;
    }
    &__logo {
        width: 179px;
        height: 26px;
        margin-left: -50px;
        img {
            width: 100%;
        }
    }
    &__search {
        margin-left: 60px;
        img {
            width: 28px;
            height: 28px;  
            &:hover {
                opacity: .8;
               }
        }
    }
    &__menu-ico {
        display: flex;
        justify-content: end;
        width: 300px;
        margin-left: 60px;
        ul {
           padding: 0;
           margin-right: 40px;
           &:last-child {
            margin-left: 0;
           }
           &:nth-child(3) {
            margin-right: 15px;
           }
           &:hover {
            opacity: .8;
           }
        }
        img {
            width: 28px;
            height: 28px;  
        }
        a {
            @include formText (700, 15px, 2);
            text-decoration: none;
            position: relative;
        }
        &__star {
            position: absolute;
            width: 20px;
            height: 20px;
            background-color: $black;
            border-radius: 50%;
            top: -17px;
            right: -12px;
            p {
                color: $white;
                text-align: center;
                font-size: 10px;
            }
        }
        &__bag {
            position: absolute;
            width: 20px;
            height: 20px;
            background-color: $black;
            border-radius: 50%;
            top: -17px;
            right: -10px;
            p {
                color: $white;
                text-align: center;
                font-size: 10px;
            }
        }
    }
}

.menu-desktop {
    list-style: none;
}
.menu-desktop__head {
    color: black;
    text-decoration: none;
    span {
        border: 5px solid transparent; border-top: 5px solid $black;
        margin-left: 10px;
        position: relative;
        top: 11px;
    }
    &::after {
        content: '';
        display: block;
        width: 0;
        height: 1px;
         background: #000;
        transition: width .3s;
        }
        &:hover::after {
            width: 100%;
            }
        &:first-child {
         margin-left: 0;
        }
}
