.product {
    margin-bottom: 70px;
    padding: 0 15px;
    h2 {
        text-align: center;
        @include formText (600, 2.5rem, 1.3);
        margin-bottom: 16px;
    }
    &__p {
        text-align: center;
        @include formText (500, 18px, 1.6);
        color: #7e7e7e;
        margin-bottom: 60px; 
    }
}
 

.card {
    padding: 0 15px;
    min-width: 280px; 
}

.card__item {
    position: relative;
    margin-bottom: 20px;
    max-width: 540px;
    &:hover .card-menu{
        right: 17px;
        opacity: 1;
    }
    img {
        width: 100%;
        margin-bottom: 20px; 
    }
    &__price {
        @include formText(700, 16px, 1.6);
        margin-bottom: 10px;
        text-align: center;
        span {
           @include formText(500, 13px, 1.6);
           color: #7e7e7e;
           text-decoration: line-through;
           margin-right: 15px;
        }
    }
    &__name {
        @include formText(500, 15px, 1.3);
        margin-bottom: 10px;
        text-align: center;
    }
    &__reviews {
        @include formText(400, 14px, 1);
        color: #7e7e7e;
        text-align: center;
        &__text {
            margin-left: 10px;
        }
    }
    &_sale {
        position: absolute;
        width: 62px;
        height: 26px;
        background-color: #4e7661;
        border-radius: 3px;
        top: 10px;
        left: 30px;
        p {
            color: $white;
            font-weight: 700;
            text-align: center;
            padding-top: 4px;
        }
    }  
}
.new {
    background-color: #d3bf0b;
}

.slick-dots {
    list-style: none;
    display: flex;
    justify-content: center;
    padding: 0;
    li {
        margin-left: 20px;
        &:nth-child(1){
            margin: 0;
        }
    }
}
.slick-dots button {
    width: 8px;
    height: 8px;
    background-color: $black;
    border-radius: 50%;
    color: transparent;
}

.slick-active button {
    width: 14px;
    height: 14px;
    background-color: $white;
    border-radius: 50%;
    border: 1px solid $black;
    color: transparent;
}

.card-menu {
    position: absolute;
    top: 100px;
    right: 0px;
    opacity: 0;
    transition: 1s;
    
    &__item {
        width: 48px;
        height: 48px;
        background-color: white;
        border-radius: 50px;
        margin-bottom: 10px;
        text-align: center;
        svg {
            width: 50%;
            padding-top: 12px;
        }
        &:hover {
            background-color: $black;
            fill: $white;
        }
        
    }
}
.star {
    svg {
        width: 13px;
        fill: #4e7661;
    }
}
