
.galery-card {
    grid-template-columns: minmax(445px, 570px) repeat(3, minmax(191.6px, 250px));
    grid-template-rows: repeat(2, auto);
    row-gap: 30px;
    column-gap: 30px;
    &__baner {
        grid-column: 1;
        grid-row: 1/3;
        img {
            width: 100%;
        }
        &__text {
            position: absolute;
            width: 416px;
            top: 70%;
            left: 10%;
        }
    }
}
.galery-card-menu {
    position: absolute;
    top: 45px;
    right: 0px;
    opacity: 0;
    transition: 1s;
    
    &__item {
        width: 48px;
        height: 48px;
        background-color: white;
        border-radius: 50px;
        margin-bottom: 10px;
        text-align: center;
        svg {
            width: 50%;
            padding-top: 12px;
        }
        &:hover {
            background-color: $black;
            fill: $white;
        }
        
    }
}
