.galery {
    margin-bottom: 50px;
    padding: 0 15px;
    h2 {
        @include formText (600, 40px, 1.3);
        margin: 16px;
        text-align: center;
    }
    &__text {
        @include formText (500, 18px, 1.66);
        color: #7e7e7e;
        max-width: 462px;
        margin: auto;
        text-align: center;
        margin-bottom: 50px; 
    }
}
.galery-card {
    display: grid;
    grid-template-columns: minmax(290px, 542px);
    grid-template-rows: minmax(348px, 545px) repeat(6, auto);

    justify-content: center;
    row-gap: 30px;
    &__baner {
        position: relative;
        width: 100%;
        background: url(../i/galery-baner.jpeg) no-repeat;
        background-size: 100%;
        &__text {
            position: absolute;
            width: 190px;
            top: 30%;
            left: 20%;
            h3 {
                @include formText (600, 40px, 1.3);
                margin-bottom: 16px;
                color: $white;
            }
            p {
                @include formText (500, 18px, 1.6);
                margin-bottom: 30px;
                color: $white;
            }
        }
        &__btn {
            margin-left: 0;
        }
    }
    &__item {
        width: 100%;
        position: relative;
        &:hover .galery-card-menu{
            right: 17px;
            opacity: 1;
        }
        img {
            width: 100%;
            margin-bottom: 10px;
        }
        &__price {
            @include formText(700, 16px, 1.6);
            margin-bottom: 7px;
            text-align: center;
            span {
               @include formText(500, 13px, 1.6);
               color: #7e7e7e;
               text-decoration: line-through;
               margin-right: 10px;
            }
        }
        &__name {
            @include formText(500, 15px, 1.3);
            margin-bottom: 7px;
            text-align: center;
        }
        &__reviews {
            @include formText(400, 14px, 1);
            color: #7e7e7e;
            text-align: center;
            &__text {
                margin-left: 10px;
            }
        }
        &_sale {
            position: absolute;
            width: 62px;
            height: 26px;
            background-color: #4e7661;
            border-radius: 3px;
            top: 10px;
            left: 30px;
            p {
                color: $white;
                font-weight: 700;
                text-align: center;
                padding-top: 4px;
            }
        }  
        &_sale {
            position: absolute;
            width: 62px;
            height: 26px;
            background-color: #4e7661;
            border-radius: 3px;
            top: 10px;
            left: 15px;
            p {
                color: $white;
                font-weight: 700;
                text-align: center;
                padding-top: 4px;
            }
        }
    }
}
.galery-new {
    background-color: #d3bf0b;
}
.galery-card-menu {
    position: absolute;
    top: 100px;
    right: 0px;
    opacity: 0;
    transition: 1s;
    
    &__item {
        width: 48px;
        height: 48px;
        background-color: white;
        border-radius: 50px;
        margin-bottom: 10px;
        text-align: center;
        svg {
            width: 50%;
            padding-top: 12px;
        }
        &:hover {
            background-color: $black;
            fill: $white;
        }
        
    }
}
