.footer {
    padding: 80px 25px;
    &__link {
        grid-template-columns: 216px 216px 216px;
        grid-template-rows: auto auto;
        gap: 0px;
        column-gap: 40px;
        &__email {
            grid-column: 1/4;
            p {
                max-width: 710px;
            }
            &__form {
                input {
                    width: 575px;
                }
            }
        }
    }
    &__soc {
        grid-template-columns: auto auto;
        grid-template-rows: auto auto;
        column-gap: 60px;
        row-gap: 30px;
        &__logo {
            grid-column: 1/3;
            grid-row: 1/2;
            margin: auto;
        }
        &__link {
            grid-column: 1/2;
            
        }
        &__pay {
            grid-column: 2/3;
        }
    }
}