.sponsor {
    background: #edf1f0;

    h3 {
        @include formText (600, 40px, 1.3);
        padding-top: 80px;
        margin-bottom: 50px;
        text-align: center;
    }
    &__card {
        max-width: 738px;
        margin: auto;
        margin-bottom: 30px;
        padding: 0 15px;
        &__item {
            max-width: 339px;
            margin-left: 20px;
            img {
                display: block;
                margin: auto;
                width: 150px;
                margin-bottom: 16px;
            }
            p {
                @include formText (600, 20px, 1.6);
                text-align: center;
                margin-bottom: 30px;
            }
        }
    }
}

.sponsor .slick-dots {
    padding-bottom: 70px;
}
.sponsor .slick-active button {
    background-color: #edf1f0;
}