.discover {
    padding: 0 15px;
    margin-bottom: 60px;
    h2 {
        @include formText (600, 40px, 1.3);
        text-align: center;
        margin-bottom: 16px;
    }
    &__text {
        @include formText (500, 18px, 1.6);
        margin: auto;
        text-align: center;
        margin-bottom: 60px;
        color: #7e7e7e;
        max-width: 530px;
    }
}
.discover-card {
    display: grid;
    grid-template-columns: minmax(290px, 516px);
    gap: 30px;
    justify-content: center;
    &__item {
        img {
            width: 100%;
            margin-bottom: 30px;
        }
        &__name {
            text-decoration: none;
            @include formText (600, 24px, 1.3);
            color: $black;
            text-align: center;
            h3 {
                margin-bottom: 16px;
            }
        }
        &__link {
            text-decoration: none;
            @include formText (600, 15px, 1.6);
            color: $black;
            text-align: center;
            color: $black;
            position: relative;
            span {
                img {
                    width: 16px;
                    margin-bottom: -4px;
                    margin-left: 10px;
                    
                }
            }
        }
    }
}