.header {
    display: grid;
    grid-template-columns: auto 1fr auto;
    justify-items: center;
    align-items: center;
    height: 80px;
    padding: 0 15px;
    &__menu {
        img {
            width: 28px;
            height: 28px;  
        }
    }
    &__menu-links {
        display: none;
    }
    &__logo {
        width: 179px;
        height: 26px;
        img {
            width: 100%;
        }
    }
    &__menu-ico {
        display: none;
    }
    &__search {
        img {
            width: 28px;
            height: 28px;  
        }
    }
}
