
.galery-card {
    grid-template-columns: repeat(3, 216px);
    grid-template-rows: 850px repeat(2, auto);
    justify-content: center;
    row-gap: 30px;
    column-gap: 30px;
    &__baner {
        position: relative;
        width: 100%;
        grid-column: 1/4;
        background-position: 0% 35%;
        &__text {
            position: absolute;
            top: 70%;
            left: 10%;
        }
    }
}
.galery-card-menu {
    position: absolute;
    top: 30px;
    right: 0px;
    opacity: 0;
    transition: 1s;
    
    &__item {
        width: 48px;
        height: 48px;
        background-color: white;
        border-radius: 50px;
        margin-bottom: 10px;
        text-align: center;
        svg {
            width: 50%;
            padding-top: 12px;
        }
        &:hover {
            background-color: $black;
            fill: $white;
        }
        
    }
}

