
.galery-card {
    grid-template-columns: 370px repeat(3, 157px);
    grid-template-rows: repeat(2, auto);
    row-gap: 30px;
    column-gap: 30px;
    &__baner {
        grid-column: 1;
        grid-row: 1/3;
        transition: background-size 1s;
        &:hover {
            background-size: 120%;
        }
        &__text {
            position: absolute;
            width: 190px;
            top: 55%;
            left: 10%;
        }
    }
}
.galery-card-menu {
    position: absolute;
    top: 20px;
    right: 0px;
    opacity: 0;
    transition: 1s;
    
    &__item {
        width: 35px;
        height: 35px;
        background-color: white;
        border-radius: 50px;
        margin-bottom: 10px;
        text-align: center;
        svg {
            width: 50%;
            padding-top: 8px;
        }
        &:hover {
            background-color: $black;
            fill: $white;
        }
        
    }
}
