.img-box {
    padding: 0 30px;
    max-width: 544px;
    margin: auto;
    &__item {
        &__card {
            max-width: 242px;
            overflow: hidden;
            img {
                width: 100%;
                transition: 1s;
                display: block;
                &:hover {
                    transform: scale(1.2);
                }
            }  
        }
    }
}
.img-box .slick-slide {
    margin-left: 15px;
    &:nth-child(1) {
        margin-left: 0;
    }
}
.img-box .slick-list {
    margin-bottom: 40px;
}