.baner {
    display: grid;
    grid-template-columns: minmax(320px, 708px);
    padding: 0 15px;
    gap: 25px;
    margin-bottom: 80px;
    justify-content: center;
    &__right {
        background: url(../i/baner/banner-01.jpeg) no-repeat;
        background-size: cover;
        background-position: center;
        height: 420px;
        h5 {
            @include formText (600, 20px, 1.3);
            padding-top: 30px;
            margin-left: 30px;
            margin-bottom: 10px
        }
        h2 {
            @include formText (600, 34px, 1.3);
            margin-left: 30px;
            margin-bottom: 40px;
            max-width: 310px;
        }
        &__btn {
            background-color: $white;
            @include formText (600, 16px, 1.6);
            box-shadow: 0 4px 9px rgba(0,0,0,.05);
            padding: 12px 25px;
            text-align: center;
            text-decoration: none;
            border-radius: 5px;
            margin-left: 30px;
            &:hover {
                background-color: $black;
                color: $white;
            }
        }
    }
    &__left {
        background: url(../i/baner/banner-02.jpeg) no-repeat;
        background-size: cover;
        background-position: center;
        height: 420px;
        h2 {
            @include formText (600, 34px, 1.3);
            padding-top: 30px;
            margin-left: 30px;
            margin-bottom: 10px;
        }
        p {
            @include formText (500, 15px, 1.3);
            max-width: 236px;
            margin-left: 30px;
            margin-bottom: 25px;
        }
        &__btn {
            background-color: $white;
            @include formText (600, 16px, 1.6);
            box-shadow: 0 4px 9px rgba(0,0,0,.05);
            padding: 12px 25px;
            text-align: center;
            text-decoration: none;
            border-radius: 5px;
            margin-left: 30px;
            &:hover {
                background-color: $black;
                color: $white;
            }
        }
    }

}